async function addMovies() {
  
    try {
      const docRef = await addDoc(collection(db, MOVIS_STORAGE_KEY), {
        movie: inputAdd.value,
    });
      console.log("Document written with ID: ", docRef.id);
    } catch (e) {
      console.error("Error adding document: ", e);
    }
}


